p {
    font-family: Lato;
  }
  
  table {
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  font-size: 14px;
  border-collapse: collapse;
  text-align: center;
  }
  th {
  background: #AFCDE7;
  color: black;
  }
  th, td {
  border-style: solid;
  border-width: 0 1px 1px 0;
  border-color: white;
  }
  td {
  background: #D8E6F3;
  }
  
  img.product{
    width: 120px;
  }
  
  td a {
    text-decoration: none;
    color: black;
    font-style: italic;
  }
  